




import Vue from "vue";
import Component from "vue-class-component";
import List from "@/components/list.vue";
import { Categoria, Common } from "@/api";
import { ErrorType, OnError } from "@/handler-error";

@Component({
  components: {
    List,
  },
})
export default class extends Vue {
  private listData!: Common[];

  private created() {
    const tipo = (this.$route.query.tipo || "moveis") as
      | "moveis"
      | "artes"
      | "decoracoes";

    switch (tipo) {
      case "moveis": {
        const data = Categoria.getCategorias();
        this.listData = data;
        break;
      }
      default: {
        OnError(
          this.$router,
          ErrorType.Error,
          {
            name: "Sem produtos para mostrar",
            message: "Essa categoria ainda está vazia.",
          },
          false
        );
      }
    }
  }
}
